.card-details-labels {
  margin: 0 8px 8px 0;
  .labels-container {
    .secondary-btn {
      display: flex;
      border-radius: $borderRad1;
      border: none;
      justify-content: center;
      align-items: center;
      width: 32px;
      padding: 0;
      height: 32px;
      svg {
        width: 16px;
        height: 16px;
        path {
          color: $clr4;
        }
      }
    }
  }
}

.color-palette {
  width: 100%;
  margin-bottom: 1.5rem;
  display: flex;
  flex-wrap: wrap;
  label[name='label-color'] {
    width: 48px;
    height: 32px;
    margin: 0 rem(8px) rem(8px) 0;
    border-radius: 3px;
    cursor: pointer;
    &:hover {
      filter: brightness(90%);
    }
  }
  input[type='radio'] {
    opacity: 0;
    position: absolute;
    width: 0;
    height: 0;
  }
  svg {
    z-index: 2;
  }
}

.extra {
  display: grid;
  grid-template-columns: repeat(3, 28px);
  grid-gap: 6px 8px;
  gap: 6px 8px;
}

.image-palette {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  label[name='label-color'] {
    width: 28px;
    height: 28px;
    margin: 0 8.4px 0.5rem 0;
    border-radius: 3px;
    cursor: pointer;
    &:hover {
      filter: brightness(90%);
    }
  }
  input[type='radio'] {
    opacity: 0;
    position: absolute;
    width: 0;
    height: 0;
  }
  svg {
    z-index: 2;
  }
}
.label {
  cursor: pointer;
  color: $clr2;
  border-radius: 3px;
  box-sizing: border-box;
  display: block;
  float: left;
  font-weight: 600;
  height: 32px;
  line-height: 32px;
  margin: 0 4px 4px 0;
  min-width: 40px;
  padding: 0 12px;
  width: auto;
  &:hover {
    opacity: 0.8;
  }
}
