.home-page-container {
  background: linear-gradient(0deg, #fff, #eae6ff 100%);
  font-family: 'Charlie Text', sans-serif;
  font-size: 20px;
  margin: auto;
  .first-home-page-container {
    max-width: 1080px;
    display: flex;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    padding-top: 7rem !important;
    padding-bottom: 5rem !important;
    align-items: center;
    h1 {
      font-size: 3rem;
      line-height: 1.25;
    }
    .first-part-txt {
    }
    .hero-img-pic img {
      margin-left: -8.33333333%;
    }

    .form-control {
      display: block;
      width: 100%;
      height: auto;
      padding: 0.375rem 0.75rem;
      font-size: 1.25rem;
      line-height: 1.5;
      color: #495057;
      background-color: #fff;
      background-clip: padding-box;
      border: 1px solid #ced4da;
      border-radius: 6px;
      -webkit-transition: border-color 0.15s ease-in-out,
        box-shadow 0.15s ease-in-out;
      transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
    }
    .btn-homepage-signup {
      display: flex;
      width: 80%;
      :nth-child(1) {
        margin: 5px;
        flex-basis: 30%;
        -webkit-transition: border-color 0.15s ease-in-out,
          box-shadow 0.15s ease-in-out;
        transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
        margin-right: 15px;

        :nth-child(2) {
          flex-basis: 40%;
        }
      }

      .btn-home-page-submit {
        color: #0065ff;
        background-color: transparent;
        background-image: none;
        border: 1px solid #0065ff;
        font-size: 1.25rem;
        margin: 2.5rem 0;
        line-height: 2.75rem;
        letter-spacing: 1px;
        font-weight: 400;
        text-align: center;
        white-space: nowrap;
        vertical-align: middle;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        padding: 0.375rem 0.75rem;
        font-size: 1.25rem;
        line-height: 1.5;
        border-radius: 6px;

        &:hover {
          border-radius: 6px;
          color: #fff;
          background-color: #0065ff;
          border: 1px solid transparent;
        }
      }
      .introduction-signup {
      }
    }
  }
  .info-btn {
    color: #fff;
    background-color: #0065ff;
    margin: 1rem;
  }

  .nav-btns a:last-child {
    border-radius: 6px;
    border: none;
    outline: none;
    font-size: 1.25rem;
    line-height: 2.75rem;
    letter-spacing: 1px;
    color: #fff;
    background-color: #0065ff;
  }
  .second-home-page-container {
    max-width: 1080px;
    text-align: center !important;
    padding-top: 5rem !important;
    padding-bottom: 5rem !important;
    margin: auto;
  }
  .info-btn {
    display: inline-block;
    font-weight: 400;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border: 1px solid transparent;
    padding: 0.375rem 0.75rem;
    font-size: 1.25rem;
    line-height: 1.5;
    border-radius: 6px;
    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
      border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
    color: #fff;
    background-color: #0065ff;
    &:hover {
      // font-size: 1.25rem;
      // line-height: 2.75rem;
      color: #fff;
      background-color: #0047b1;
    }
  }
}
