.search-bar-container {
  color: #172b4d;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans,
    Ubuntu, Droid Sans, Helvetica Neue, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 20px 0 9px 7px;
  label {
    color: #5e6c84;
    display: block;
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;
    margin-bottom: 4px;
    margin-top: 12px;
  }
  input {
    margin-top: 0;
  }
  .left-search-bar-container {
    flex-wrap: wrap;
    display: flex;
    flex-direction: row;
    .sort-by-container {
      display: flex;
      margin-right: 4px;
      flex-direction: column;
      .select-sort-by-workspace {
        margin-left: 0px;
        width: 100%;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
      }
    }
    .filter-by-container {
      display: flex;
      margin-right: 4px;
      flex-direction: column;
      .select-filter-by-workspace {
      }
    }
  }
  .search-all-work-space {
    display: flex;
    flex-direction: column;
    .search-all-work-space-form {
      .search-all-work-filter-input {
      }
    }
  }
}

