.home-header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 100;
  transition: background-color 1s ease 0s;
  &.visibleBg {
    background-color: #fff;
    box-shadow: 0 0 10px rgba(black, 0.3);
  }
  .user-details-header {
    .user-logo-in-app-header {
      border: none;
      .letter-logo-workspace-header {
        height: 32px;
        background: linear-gradient(#d42405, #f03000);
        width: 32px;
        line-height: 32px;
        border-radius: 3rem;
        text-transform: uppercase;
        color: white !important;
      }
    }
  }

  nav {
    padding: 16px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .logo {
      display: flex;
      align-items: center;
      font-family: atlata;
      font-weight: 750;
      font-size: 36px;
      line-height: 36px;
      margin-right: auto;
      color: #293856;
      svg {
        width: 26px;
        height: 26px;
        margin-right: 8px;
      }
    }
    .nav-btns {
      display: flex;
      align-items: center;
      > * {
        padding: 0 10px;
        transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out;
        line-height: 36px;
        font-size: 17.5px;
      }
      .login-btn {
        letter-spacing: 1px;
        color: #0065ff;
        // font-weight: 500;
        &:hover {
          color: #0051cc;
          text-decoration: underline;
        }
      }
      .nav-btns > * a {
        list-style-type: none;
      }
      .signup-btn {
        letter-spacing: 1px;
        color: #fff;
        background-color: #0065ff;
        &:hover {
          background-color: #0051cc;
        }
      }
    }
  }
}
