.MuiPaper-root {
  padding: 2rem;
  width: 200px;
  max-height: 100%;
  position: relative;
}
.popover-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0.4rem;
}

.pop-over {
  background: #fff;
  border-radius: 3px;
  box-shadow: 0 8px 16px -4px #091e4240, 0 0 0 1px #091e4214;
  position: fixed;
  z-index: 70;
  padding: 0 12px 12px;
  min-width: 20rem;
  margin-right: 4px;
  max-width: 100%;
  &.clean {
    padding: unset;
  }
  &.menu,
  &.menu-popovers {
    background-color: #f4f5f7;
    height: calc(100vh - 40px);
    overflow: auto;
  }
  &.menu {
    margin-inline-end: 0;
    top: 40px;
    right: 0;
    animation: open-menu 0.1s ease-in;
    animation-fill-mode: forwards;
    -webkit-animation-fill-mode: forwards;
  }
  .back {
    position: absolute;
    top: 14px;
    left: 24px;
    font-size: 20px;
    color: #6b778c;
    cursor: pointer;
    &:hover {
      color: $clr4;
    }
    svg {
      width: 18px;
    }
  }
  .pop-over-header {
    display: flex;
    align-items: center;
    text-align: center;
    color: $clr3;
    display: block;
    line-height: 40px;
    border-bottom: 1px solid rgba(9, 30, 66, 0.13);
    margin: 0 0 12px;
    overflow: hidden;
    padding: 0 32px;
    position: relative;
    text-overflow: ellipsis;
    h3 {
      flex-grow: 1;
      font-size: 14px;
      font-weight: 400;
    }
    button {
      position: absolute;
      top: 50%;
      right: 0;
      border: none;
      background-color: transparent;
      padding: 0;
      margin: 0;
      outline: none;
      cursor: pointer;
      transform: translateY(-50%);
      line-height: 16px;
      color: #6b778c;
      &:hover {
        color: $clr4;
      }
      .svg {
        width: 16px;
        height: 16px;
      }
    }
    &.clean {
      position: absolute;
      top: 0;
      right: 0;
      border-bottom: none;
      padding: 12px;
      button {
        position: static;
      }
    }
    &.menu,
    &.menu-popovers {
      h3 {
        flex-grow: 1;
        line-height: 20px;
        margin: 14px 32px;
        font-size: 16px;
        color: $clr4;
        font-weight: 600;
      }
      button {
        top: 14px;
        transform: none;
        svg {
          width: 20px;
          height: 20px;
        }
      }
    }
  }
  .pop-over-content {
    h4 {
      color: $clr3;
      font-weight: 500;
      letter-spacing: 0.04em;
      font-size: 12px;
      margin-bottom: 0.5rem;
    }
  }
  .pop-over-label {
    font-size: 12px;
    font-weight: 700;
    color: $clr3;
  }
}

.pop-over-input {
  margin: 4px 0 12px 0;
  width: 100%;
  outline: none;
  border: none;
  box-shadow: inset 0 0 0 2px #dfe1e6;
  background-color: #fafbfc;
  border-radius: 3px;
  display: block;
  line-height: 20px;
  padding: 8px 12px;
  &::placeholder {
    color: $clr3;
  }
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 9;
}
.element-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
@include for-mobile-layout {
}

@keyframes open-menu {
  from {
    transform: translateX(22.5rem);
  }
  to {
    transform: translateX(0);
  }
}
